import React from 'react';
import Button from '@material-ui/core/Button';
import clsx from  'clsx';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles({
    btn: {
        width: '120px',
        padding: '4px 0px',
        zIndex: 1,
        position: 'relative',
        
    },
    login: {
        background: 'linear-gradient(90deg, #05F1C7 0%, #00DDA2 100%)',
        marginRight: '16px'
    },
    btn2: {

    }
})

const TopHeader = () => {
    
    const classes = useStyles()
    return (
        <div className="top-header">
            <div>
                <a href="https://app.instarabbit.co/login" target="__blank">
                    <Button className={clsx(classes.btn, classes.login)}>Login</Button>
                </a>
                <a href="https://instarabbit.co/pricing" target="__self">
                    <Button>Try for Free</Button>
                </a>
            </div>
        </div>
    )
}

export default TopHeader;